var periodData = require("./periods");

module.exports = {
  /**
   * Converts Gregorian calendar year to Japanese calendar year
   */
  japaneseYear: function japaneseYear(gregorianYear) {
    var gregorianYearNum;

    if (gregorianYear instanceof Date) {
      gregorianYearNum = gregorianYear.getFullYear();
    } else if (typeof gregorianYear === "number") {
      gregorianYearNum = gregorianYear;
    } else {
      throw new TypeError("Expected a Date or Number");
    }

    var periodYears = periodData.sort(function (a, b) {
      return b.startYear - a.startYear;
    }).map(function (period) {
      return period.startYear;
    });
    if (gregorianYearNum < periodYears[periodYears.length - 1]) return null;
    var exactPeriodYear = periodYears.find(function (periodYear, i) {
      if (i === 0) return periodYear <= gregorianYearNum;
      if (i === periodYears.length - 1) return true;
      return periodYear <= gregorianYearNum && periodYears[i - 1] > gregorianYearNum;
    });
    var foundPeriod = periodData.find(function (period) {
      return exactPeriodYear === period.startYear;
    });
    var updatedPeriod = Object.assign({
      currentJapaneseYear: gregorianYearNum - foundPeriod.startYear + 1
    }, foundPeriod);
    return updatedPeriod;
  },

  /**
   * Converts Japanese calendar year to Gregorian year range
   * @param {String} year
   * @return {object}
   */
  gregorianYearRange: function gregorianYearRange(japanesePeriod) {
    if (typeof japanesePeriod !== "string") {
      throw new TypeError("Expected a String");
    }

    var yearRange = null;
    var foundPeriod = periodData.find(function (period) {
      return period.names.kanji === japanesePeriod || period.names.hiragana === japanesePeriod || period.names.english === japanesePeriod;
    });

    if (foundPeriod) {
      var previousPeriod = periodData[periodData.indexOf(foundPeriod) - 1];
      var previousYear = previousPeriod ? previousPeriod.startYear - 1 : null;
      yearRange = {
        startYear: foundPeriod.startYear,
        endYear: previousYear
      };
    }

    return yearRange;
  }
};